export function getNowtimestamp() {
	let milliseconds = Date.now();
	return parseInt(milliseconds / 1000);
}

/**
 *	 
 *  {Object} max
 */

export function randomNum(max) {
	if (!max) {
		max = 999999;
	}
	return Math.floor(Math.random() * (max + 1));
}

