// 
export function getNowDay(split='-'){
	let date = new Date();
	let year = date.getFullYear(); 
	let month = date.getMonth() + 1;
	let day = date.getDate();
	month = (month > 9) ? month : ("0" + month);
	day = (day < 10) ? ("0" + day) : day;
	return year + split + month + split + day;
}
// +n
export function addNowDay(datetime, days) {
	let myDate = new Date();
	if (datetime == 0) {
		datetime = myDate.getFullYear() + '/' + (Number(myDate.getMonth()) + 1) + '/' + myDate.getDate()
	}
	// console.log(datetime);
	let old_time = new Date(datetime.replace(/-/g, "/")); //js2013-01-31,2013/01/31
	let fd = new Date(old_time.valueOf() + days * 24 * 60 * 60 * 1000); //
	let new_time = fd.getFullYear() + "-";
	let month = fd.getMonth() + 1;
	if (month >= 10) {
		new_time += month + "-";
	} else {
		//100
		new_time += "0" + month + "-";
	}
	if (fd.getDate() >= 10) {
		new_time += fd.getDate();
	} else {
		//100
		new_time += "0" + fd.getDate();
	}
	return new_time; //2013-01-02
}

// 
export function gethoursInfo(hours) {
	let dataList = [];
	let myDate = new Date();
	if(hours == 0){
		hours = myDate.getHours();
	}
	
	if (hours >= 18) {
		dataList.push({value:''});
		return dataList;
	}
	for (let ii = 8; ii <= 17; ii++) {
		if (ii >= 8 && ii >= hours) {
			if (ii < 9) {
				dataList.push({value:'0' + ii + ':00' + '~' + '0' + (ii + 1) + ':00'});
			} else if (ii == 9) {
				dataList.push({value:'0' + ii + ':00' + '~' + (ii + 1) + ':00'});
			} else {
				dataList.push({value:ii + ':00' + '~' + (ii + 1) + ':00'});
			}
		}
	}
	return dataList;
}
