/*  */
export function showLoading(message) {
  if (wx.showLoading) {    //  1.1.0 6.5.6
    wx.showLoading({
      title: message, mask: true
    });
  } else {    // Toast20
    wx.showToast({
      title: message, icon: 'loading', mask: true, duration: 20000
    });
  }
}
export function hideLoading() {
  if (wx.hideLoading) {    //  1.1.0 6.5.6
    wx.hideLoading();
  } else {
    wx.hideToast();
  }
}
